import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import {
	Container,
	Col,
	Row,
	Form,
	Button,
	Alert
} from "react-bootstrap"
import axios from 'axios'

export default class RegistrationPage extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			gender: "",
			relation: "",
			raisehands: "",
			visitedResources: "",
			announcementsView: "",
			discussion: "",
			result: ""
		}
	}

	handleRegistation = (e) => {
		e.preventDefault();
		axios({
			url: 'http://localhost:3000/registration',
			method: 'POST',
			data: {
				gender: this.state.gender,
				relation: this.state.relation,
				raisehands: this.state.raisehands,
				visitedResources: this.state.visitedResources,
				announcementsView: this.state.announcementsView,
				discussion: this.state.discussion
			}
		}).then((response) => {
			const data = response.data
			this.setState({
				result: data,
			});
			// This needs to be removed later
			console.log(this.state.result)
			if (this.state.result.success) {
				this.setState({
					gender: "",
					relation: "",
					raisehands: "",
					visitedResources: "",
					announcementsView: "",
					discussion: "",
				})
			}
		}).catch((error) => {
			console.log(error.response.request);
		})
	};

	handleLogin = (e) => {
		e.preventDefault();
		this.props.history.push('/login')
	}

	handleInputs = (e) => {
		this.setState({
			[e.target.name]: e.target.value
		})
	};


	render() {
		const EmailError = (
			<Alert variant="danger">
				{this.state.result.email}
			</Alert>
		)

		const PasswordError = (
			<Alert variant="danger">
				{this.state.result.password}
			</Alert>
		)

		const NameError = (
			<Alert variant="danger">
				{this.state.result.name}
			</Alert>
		)

		const RegistrationComplete = (
			<Alert variant="success">
				{this.state.result.success}
			</Alert>
		)

		return (
			<div>
				<Container>
					<Row style={{ marginTop: "2em", marginBottom: '11.1em' }}>
						<Col className="align-self-center md-6" style={{ paddingBottom: '5em' }}>
							<p style={{ fontSize: '3.32em' }}>Input your information and obtain <u style={{ color: '#1E38BF' }}><strong>your prediction</strong></u></p>
						</Col>
						<Col className="align-self-center md-6" style={{ paddingLeft: '4em', paddingTop: '5em' }}>
							<Form style={{ border: "1px solid #fff", padding: "50px 60px", backgroundColor: '#fff', borderRadius: '0.8em', WebkitBoxShadow: "0px 0px 20px -1px rgba(0,0,0,0.75)", MozBoxShadow: "0px 0px 20px -1px rgba(0,0,0,0.75)", boxShadow: '0px 0px 20px -1px rgba(0,0,0,0.75)' }}>

							<Form.Group controlId="exampleForm.ControlSelect1">
									<Form.Label>Gender</Form.Label>
									<Form.Control as="select" name="gender" id="gender" defaultValue="Choose..." value={this.state.gender} onChange={this.handleInputs} required>
										<option>Not specified</option>
										<option>Male</option>
										<option>Female</option>
									</Form.Control>
								</Form.Group>

								{/* display if error exists */}
								{this.state.result.name ? NameError : null}

								<Form.Group controlId="formRelation">
									<Form.Label>Relation</Form.Label>
									<Form.Control type="text" name="relation" id="relation" placeholder="e.g. Father" value={this.state.relation} onChange={this.handleInputs} required />
								</Form.Group>

								{/* display if error exists */}
								{this.state.result.name ? NameError : null}


								<Form.Group controlId="formRaisehands">
									<Form.Label>Raisehands</Form.Label>
									<Form.Control type="text" name="raisehands" id="raisehands" placeholder="e.g.5..." value={this.state.raisehands} onChange={this.handleInputs} required />
								</Form.Group>

								<Form.Group controlId="formVistedResources">
									<Form.Label>visitedResources</Form.Label>
									<Form.Control type="text" name="visitedResources" id="visitedResources" placeholder="e.g.5..." value={this.state.visitedResources} onChange={this.handleInputs} required />
								</Form.Group>

								<Form.Group controlId="formAnnouncementsView">
									<Form.Label>AnnouncementsView</Form.Label>
									<Form.Control type="text" name="announcementsView" id="AnnouncementsView" placeholder="e.g.5..." value={this.state.announcementsView} onChange={this.handleInputs} required />
								</Form.Group>

								<Form.Group controlId="formDiscussion">
									<Form.Label>Discussion</Form.Label>
									<Form.Control type="text" name="discussion" id="discussion" placeholder="e.g.5..." value={this.state.discussion} onChange={this.handleInputs} required />
								</Form.Group>

								{/* display if error exists */}
								{this.state.result.email ? EmailError : null}
								{/* display if error exists */}
								{this.state.result.password ? PasswordError : null}
								
								{/* if success */}
								{this.state.result.success ? RegistrationComplete : null}

								<br></br>
								<Button className="float-right" onClick={this.handleRegistation} style={{ borderRadius: '0.7em', backgroundColor: '#1E38BF', WebkitBoxShadow: "0px 0px 20px -1px rgba(0,0,0,0.75)", MozBoxShadow: "0px 0px 20px -1px rgba(0,0,0,0.75)", boxShadow: '0px 0px 5px -1px rgba(0,0,0,0.75)', fontSize: '1.5em' }}>Predict</Button>
								<br></br>
								<br></br>
							</Form>
						</Col>
					</Row>
				</Container>
			</div>
		)
	}

}